import React from 'react';

import { Form, Input, Button, Checkbox } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';

import 'antd/dist/antd.css'; // or 'antd/dist/antd.less'
import './LoginPanel.css';

class LoginPanel extends React.Component{


    render() {

        const onFinish = (values: any) => {
            document.getElementById('components-form-demo-normal-login').appendChild( document.getElementById("csrf"));
            if(values.remember){
                document.getElementById('components-form-demo-normal-login').appendChild( document.getElementById("remember"));
            }
            document.getElementById('components-form-demo-normal-login').submit();
            // ajax.post("login",values);
        };

        return (
            <Form
                method="post"
                action="login"
                name="normal_login"
                id="components-form-demo-normal-login"
                className="login-form"
                initialValues={{ remember: true }}
                onFinish={onFinish}
            >
                <Form.Item
                    name="username"
                    rules={[{ required: true, message: 'Please input your Username, Phone, or Idcard!' }]}
                >
                    <Input name="username" prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Username/Phone/Idcard" />
                </Form.Item>
                <Form.Item
                    name="password"
                    rules={[{ required: true, message: 'Please input your Password!' }]}
                >
                    <Input
                        prefix={<LockOutlined className="site-form-item-icon" />}
                        type="password"
                        name="password"
                        placeholder="Password"
                    />
                </Form.Item>
                <Form.Item>
                    <Form.Item name="remember" valuePropName="checked" noStyle>
                        <Checkbox>Remember me</Checkbox>
                    </Form.Item>
                    <a className="login-form-forgot" href="changePassword">
                        Forgot password
                    </a>
                </Form.Item>
                <Form.Item>
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        Log in
                    </Button>
                    Or <a href="register">register now!</a>
                </Form.Item>
            </Form>
        );
    };
}
export default LoginPanel;